.element-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.display-element label, select {
  display:block;
  margin:.2em .5em;
}

.display-element label {
  text-align: center;
}

.display-element select {
  /* width: 30%; */
  border: .15em solid var(--blue);
  border-radius: .5em;
  background: var(--blue);
  color: white;
  font-size: 1.25em;
}


