.display {
  position: relative;
  width: 100%;
  height: 360px;
  margin: 0 auto;
}
.display-time {
  position: absolute;
  margin: 0 auto;
  width: 16rem;
  top: calc(50% - 2rem);
  left: calc(50% - 8rem);
  border: none;
  text-align: center;
  line-height: 4rem;
  font-size: 4rem;
}




